import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import toast from 'react-hot-toast';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Autocomplete,
  Box,
  Button,
  Card,
  CardContent,
  // CardHeader,
  // Checkbox,
  // FormControlLabel,
  FormHelperText,
  Grid,
  TextField,
  // Typography
} from '@material-ui/core';
// import FileDropzone from '../../../FileDropzone';
// import QuillEditor from '../../QuillEditor';
/* eslint-disable import/no-duplicates */
import firebase from '../../../../lib/firebase';
// import { functions } from '../../../../lib/firebase';

const VehicleCreateForm = (props) => {
  const navigate = useNavigate();
  const [brandsOptions, setBrandsOptions] = useState([]);
  const [modelsOptions, setModelsOptions] = useState([]);
  const [clientsOptions, setClientsOptions] = useState([{ value: '', name: '' }]);
  const [colors, setColors] = useState([]);
  const [isSubmit, setIsSubmit] = useState(false);
  const empty = '';

  const sorted = (array) => array.sort((a, b) => a.value.localeCompare(b.value));

  const getColor = () => {
    firebase
      .firestore()
      .collection('data')
      .doc('color')
      .get()
      .then((snapshot) => {
        const info = { id: '', value: '' };
        const data = [];
        data.push(info);
        snapshot.data().data.forEach((element) => {
          data.push(element);
        });
        setColors(sorted(data));
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const getClients = () => {
    firebase
      .firestore()
      .collection('workshop_clients')
      .get()
      .then((snapshot) => {
        const data = [];
        snapshot.forEach((element) => {
          data.push({ value: element.id, text: element.data().name });
        });
        setClientsOptions(data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    const arrayModel = [{ id: '', name: '', vehicles_count: 0 }];
    firebase
      .firestore()
      .collection('dealership_brands')
      .get()
      .then((snapshot) => {
        const data = [{ id: '', name: '', vehicles_count: 0 }];
        snapshot.forEach((doc) => {
          /* eslint-disable object-shorthand */
          data.push({ ...doc.data(), id: doc.id });
          firebase
            .firestore()
            .collection('dealership_brands')
            .doc(doc.id)
            .collection('models')
            .get()
            .then((modelBrand) => {
              modelBrand.forEach((document) => {
                /* eslint-disable object-shorthand */
                arrayModel.push({ ...document.data(), id: document.id, idBrand: doc.id, nameBrand: doc.data().name });
              });
              setModelsOptions(arrayModel);
            });
        });
        setBrandsOptions(data);
      });
    getColor();
    getClients();
  }, []);

  return (
    <Formik
      initialValues={{
        brand: '',
        model: '',
        tag: '',
        color: '',
        client_id: '',
        client_name: '',
        // submit: null
      }}
      validationSchema={Yup
        .object()
        .shape({
          brand: Yup.string().max(255).required('La marca es obligatoria'),
          model: Yup.string().max(255).required('El modelo es obligatorio'),
          tag: Yup.string().max(255).required('La placa es obligatoria'),
          color: Yup.string().max(255).required('El color es obligatorio'),
          // client_id: Yup.string().max(255).required(),
          client_id: Yup.string().max(255).required('El cliente es obligatorio'),
          client_name: Yup.string().max(255).required('El cliente es obligatorio'),
        })}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          setSubmitting(true);
          setIsSubmit(true);
          const brandName = brandsOptions.filter((brand) => brand.id === values.brand);
          values.brand_name = brandName[0].name;
          values.logo = brandName[0].logo;
          const modelName = modelsOptions.filter((model) => model.id === values.model);
          values.model_name = modelName[0].name;
          values.date = {
            registered: '',
            lastActivity: ''
          };
          values.owner = [];
          values.date.registered = firebase.firestore.FieldValue.serverTimestamp();
          values.date.lastActivity = firebase.firestore.FieldValue.serverTimestamp();
          values.owner.push(values.client_id);
          values.tag = values.tag.toString();
          let idDoc = '';

          firebase.firestore().collection('workshop_vehicles').add(values)
            .then((u) => {
              idDoc = u.id;
              return firebase.firestore().collection('workshop_clients').doc(values.client_id);
            })
            .then((query) => {
              console.log('');
              return query.get();
            })
            .then((doc) => {
              const vh = doc.data().vehicles;
              vh.push(idDoc);
              return vh;
            })
            .then((vehicles) => {
              console.log('');
              // return doc.data().vehicles
              return firebase.firestore().collection('workshop_clients').doc(values.client_id).update({
                vehicles: vehicles
              });
            })
            .then(() => {
              // success('Vehiculo Agregado', 'Se agrego correctamente el vehículo a ' + client.name);
              toast.success('Vehiculo Registrado', `Se agrego correctamente el vehículo a ${values.client_name} exitosamente!`);
              navigate('/dashboard/taller/vehiculos');
              setIsSubmit(false);
              return true;
            });
          return true;
        } catch (err) {
          console.error(err);
          toast.error('Something went wrong!');
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
          setIsSubmit(false);
          return true;
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        // isSubmitting,
        setFieldValue,
        touched,
        values
      }) => (
        <form
          onSubmit={handleSubmit}
          {...props}
        >
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              lg={12}
              md={12}
              xs={12}
            >
              <Card>
                <CardContent>
                  <Grid
                    container
                    spacing={3}
                  >
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        fullWidth
                        error={Boolean(touched.brand && errors.brand)}
                        label="Marca"
                        helperText={touched.brand && errors.brand}
                        name="brand"
                        onChange={handleChange}
                        select
                        SelectProps={{ native: true }}
                        value={values.brand}
                        variant="outlined"
                      >
                        {brandsOptions.map((brand) => (
                          <option
                            key={brand.id}
                            value={brand.id}
                          >
                            {brand.name}
                          </option>
                        ))}
                      </TextField>
                    </Grid>
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        fullWidth
                        error={Boolean(touched.model && errors.model)}
                        label="Modelo"
                        helperText={touched.model && errors.model}
                        name="model"
                        onChange={handleChange}
                        select
                        SelectProps={{ native: true }}
                        value={values.model}
                        variant="outlined"
                      >
                        <option
                          value={null}
                        >
                          {empty}
                        </option>
                        {modelsOptions.filter((models) => models.idBrand === values.brand).map((model) => (
                          <option
                            key={model.id}
                            value={model.id}
                          >
                            {model.name}
                          </option>
                        ))}
                      </TextField>
                    </Grid>
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        error={Boolean(touched.tag && errors.tag)}
                        fullWidth
                        helperText={touched.tag && errors.tag}
                        label="Placa"
                        name="tag"
                        type="text"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.tag}
                        variant="outlined"
                      />
                    </Grid>
                    <Grid
                      item
                      md={6}
                      xs={12}
                    >
                      <TextField
                        fullWidth
                        error={Boolean(touched.color && errors.color)}
                        label="Color"
                        helperText={touched.color && errors.color}
                        name="color"
                        onChange={handleChange}
                        select
                        SelectProps={{ native: true }}
                        value={values.color}
                        variant="outlined"
                      >
                        {colors.map((color) => (
                          <option
                            key={color.id}
                            value={color.value}
                          >
                            {color.value}
                          </option>
                        ))}
                      </TextField>
                    </Grid>
                    <Grid
                      item
                      md={12}
                      xs={12}
                    >
                      <Autocomplete
                        getOptionLabel={(option) => option.text}
                        isOptionEqualToValue={(option, value) => option.value === value.value}
                        // getOptionLabel={(option) => option.name}
                        onChange={(e, value) => {
                          setFieldValue('client_id', value.value);
                          setFieldValue('client_name', value.text);
                        }}
                        options={clientsOptions}
                        renderInput={(params) => (
                          <TextField
                            error={Boolean(touched.client_id && errors.client_id)}
                            fullWidth
                            label="Cliente"
                            helperText={touched.client_id && errors.client_id}
                            name="client_id"
                            variant="outlined"
                            // value={values.client_name}
                            {...params}
                          />
                        )}
                      />
                      {/* <TextField
                        fullWidth
                        label="Cliente"
                        name="client_id"
                        onChange={handleChange}
                        select
                        SelectProps={{ native: true }}
                        value={values.client_id}
                        variant="outlined"
                      >
                        {clientsOptions.map((client) => (
                          <option
                            key={client.id}
                            value={client.id}
                          >
                            {client.name}
                          </option>
                        ))}
                      </TextField> */}
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>
            <Grid
              item
              lg={2}
              md={2}
              xs={12}
            >
              {errors.submit && (
                <Box sx={{ mt: 3 }}>
                  <FormHelperText error>
                    {errors.submit}
                  </FormHelperText>
                </Box>
              )}
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'flex-end',
                  mt: 3
                }}
              >
                <Button
                  color="primary"
                  disabled={isSubmit}
                  type="submit"
                  variant="contained"
                  // onClick={() => save(values)}
                >
                  Registrar Vehículo
                </Button>
              </Box>
            </Grid>
          </Grid>
        </form>
      )}
    </Formik>
  );
};

export default VehicleCreateForm;
