import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Divider,
  TextField,
} from '@material-ui/core';
import toast from 'react-hot-toast';
import MailIcon from '../../../../icons/Save';
import firebase from '../../../../lib/firebase';

const CustomerEmailsSummary = (props) => {
  const { idVehicle, idClient, clientsOptions, vehiclesClient, vehiclesOwner, ...other } = props;
  const [client, setClient] = useState('');

  useEffect(() => {
    // getEmails();
  }, []);

  /* eslint-disable object-shorthand */
  const changeClient = async () => {
    console.log(idVehicle);
    console.log(idClient);
    console.log(client);
    const clientName = clientsOptions.find((cli) => cli.id === client);
    console.log(clientName);

    const oldClient = firebase.firestore().collection('workshop_clients').doc(idClient);

    const newClient = firebase.firestore().collection('workshop_clients').doc(client);

    const vehicle = firebase.firestore().collection('workshop_vehicles').doc(idVehicle);

    await firebase.firestore().runTransaction((transaction) => {
      console.log('');
      return transaction.get(newClient).then(async (sDoc) => {
        const newVeh = sDoc.data().vehicles;

        const remVeh = [];
        vehiclesClient.forEach((veh) => {
          if (idVehicle !== veh) {
            remVeh.push(veh);
          }
        });
        newVeh.push(idVehicle);
        vehiclesOwner.push(client);

        transaction.update(newClient, {
          vehicles: newVeh,
        });

        transaction.update(oldClient, {
          vehicles: remVeh,
        });

        transaction.update(vehicle, {
          client_id: client,
          client_name: clientName.name,
          owner: vehiclesOwner,
        });
      });
    }).then(() => {
      toast.success('Vehículo Traspasado exitosamente!');
    }).catch(() => toast.error('Ha ocurrido un error!'));
  };

  return (
    <Card {...other}>
      <CardHeader title="Traspasar Vehículo" />
      <Divider />
      <CardContent>
        <Box>
          <TextField
            fullWidth
            name="option"
            onChange={(event) => setClient(event.target.value)}
            select
            SelectProps={{ native: true }}
            value={client}
            variant="outlined"
          >
            {clientsOptions.map((option) => (
              <option
                key={option.id}
                value={option.id}
              >
                {option.name}
              </option>
            ))}
          </TextField>
          <Box sx={{ mt: 2 }}>
            <Button
              color="primary"
              startIcon={<MailIcon fontSize="small" />}
              variant="contained"
              onClick={() => changeClient()}
            >
              Traspasar
            </Button>
          </Box>
        </Box>
      </CardContent>
    </Card>
  );
};

CustomerEmailsSummary.propTypes = {
  idVehicle: PropTypes.string,
  idClient: PropTypes.string,
  clientsOptions: PropTypes.array,
  vehiclesClient: PropTypes.array,
  vehiclesOwner: PropTypes.array,
};

export default CustomerEmailsSummary;
